<template>
  <div class="mainTableContainer">
    <el-table :data="mainTable" style="width: 100%" :span-method="arraySpanMethod" border>
      <el-table-column label="       " align="center">
        <el-table-column label="" header-align="center">
          <el-table-column
            header-align="left"
            label="    "
          >
            <el-table-column label="     " header-align="center"></el-table-column>
            <el-table-column label="" header-align="center"></el-table-column>
            <el-table-column label="" header-align="center">
              <el-table-column label="" header-align="center"></el-table-column>
              <el-table-column label="" header-align="center"></el-table-column>
            </el-table-column>
            <el-table-column label="" header-align="center">
              <el-table-column label="" header-align="center"></el-table-column>
              <el-table-column label="" header-align="center"></el-table-column>
            </el-table-column>
          </el-table-column>
        </el-table-column>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    }
  },
  data() {
    return {
      mainTable: []
    };
  },
  methods: {
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if (rowIndex % 2 === 0) {
        if (columnIndex === 0) {
          return [1, 2];
        } else if (columnIndex === 1) {
          return [0, 0];
        }
      }
    }
  }
};
</script>



